<template>
  <div class="row">
    <div v-if="gettingSale" class="loading-screen bg-white flex-center">
      <div class="snippet" data-title=".dot-flashing">
        <div class="stage">
          <div class="dot-flashing"></div>
        </div>
      </div>
    </div>
    <CoolLightBox
      :items="images"
      :useZoomBar="true"
      :index="index"
      @close="index = null"
    >
    </CoolLightBox>
    <div
      class="flex-x-center w-100"
      style="padding: 0px 10px; margin-top: 30px"
    >
      <!-- <h4 class="mb-0">Yiu's Villa Supplier</h4> -->
      <div></div>
      <!-- <img src="/logo.png" width="150px" alt="" /> -->
      <p class="mb-2">
        {{ order.date }}
      </p>
    </div>
    <div class="content w-100" style="margin-bottom: 30px">
      <div class="card-product">
        <h5 style="margin: 15px 0px">Customer</h5>
        <p class="gray-bg">Order ID : {{ order.id }}</p>
        <p class="gray-bg">
          Service Plan :
          {{
            (order.delivery_company_name == "safe delivery"
              ? "Normal Plan"
              : order.delivery_company_name) || "-"
          }}
        </p>
        <p class="gray-bg">Name : {{ order.customer_name }}</p>
        <p class="gray-bg">
          Phone :
          {{ order.phone }}
        </p>
        <p class="gray-bg" v-if="order.address">
          Address :{{ order.address.address }},
          {{ order.address.city.split("_").join(" ") }},
          {{ order.address.state.split("_").join(" ") }}
        </p>
      </div>
      <div class="card-product">
        <h5 style="margin: 15px 0px">Items</h5>
        <div
          class="gray-bg item-row"
          style="position: relative"
          v-for="(product, index) in order.products"
          :key="index"
        >
          <img
            :src="product.image"
            class="item-img"
            @click="viewImage(product.image)"
            alt="Image"
            @load="loaded"
          />
          <div style="width: calc(100% - 100px); padding: 9px">
            <div
              style="
                display: flex;
                justify-content: space-between;
                align-items: center;
              "
            >
              <p
                style="
                  font-size: 15px !important;
                  display: flex;
                  align-items: center;
                "
              >
                <span class="number">
                  {{ index + 1 }}
                </span>
                <span v-if="product.name !== 'Refund'">
                  {{ product.name || "-" }}
                </span>
              </p>
            </div>

            <a
              :href="getLink(product.link)"
              v-if="product.link"
              target="_blank"
              class="sm-text code"
              style="margin-bottom: 8px; display: block"
              rel="noopener noreferrer"
            >
              {{ product.link }}

              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="14"
                height="14"
                style="position: relative; top: 2px"
                viewBox="0 0 24 24"
                fill="none"
                stroke="currentColor"
                stroke-width="2"
                stroke-linecap="round"
                stroke-linejoin="round"
                class="feather feather-external-link"
              >
                <path
                  d="M18 13v6a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V8a2 2 0 0 1 2-2h6"
                ></path>
                <polyline points="15 3 21 3 21 9"></polyline>
                <line x1="10" y1="14" x2="21" y2="3"></line>
              </svg>
            </a>
            <p v-else class="xs-text">No tracking code yet</p>
            <div
              style="
                display: flex;
                justify-content: space-between;
                align-items: flex-end;
                width: 100%;
              "
            >
              <p
                style="
                  margin-bottom: 0px !important;
                  color: rgb(var(--text), 0.7);
                "
              >
                {{ product.quantity }}x{{ product.sale_price }}
              </p>
              <p
                style="
                  font-weight: bold;
                  font-size: 18px !important;
                  color: #6951a4;
                  margin-bottom: 0px !important;
                "
              >
                {{ parseFloat(product.subtotal).toFixed(2) }}
              </p>
            </div>
            <div style="display: flex; margin-top: 5px; align-items: center">
              <span
                v-if="
                  product.quantity === product.number_of_fullfilled &&
                  product.date
                "
                class="received_date"
              >
                Received on {{ $moment(product.date).format("DD/MM/YYYY") }}
              </span>
              <span class="refund" v-if="product.name === 'Refund'">
                Refunded
              </span>
              <span
                v-if="
                  product.quantity === product.number_of_fullfilled &&
                  !product.date
                "
                class="received_date"
              >
                Received
              </span>
            </div>
          </div>
          <!-- <img
            v-if="product.quantity === product.number_of_fullfilled"
            src="/done.png"
            width="80px"
            class="done-img"
            alt=""
          /> -->

          <!-- <td>{{ product.quantity }}x{{ product.sale_price }}</td>
            <td>
              {{ parseFloat(product.subtotal).toFixed(2) }}
            </td> -->
        </div>
      </div>
      <!-- <div class="card-product">
        <h5 style="margin: 15px 0px">Items</h5>
        <table id="products">
          <tr>
            <th>No.</th>
            <th>Item</th>
            <th>Rate</th>
            <th>Subtotal</th>
          </tr>
          <tr v-for="(product, index) in order.products" :key="index">
            <td>
              {{ index + 1 }}
            </td>
            <td class="item-row">
              <img
                :src="product.image"
                class="item-img"
                @click="viewImage(product.image)"
                alt="Image"
                @load="loaded"
              />
              <div>
                <p>{{ product.name || "-" }}</p>
                <a
                  :href="getLink(product.link)"
                  v-if="product.link"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  {{ product.link }}
                </a>
              </div>
            </td>

            <td>{{ product.quantity }}x{{ product.sale_price }}</td>
            <td>
              {{ parseFloat(product.subtotal).toFixed(2) }}
            </td>
          </tr>
        </table>
      </div> -->
      <div class="card-product">
        <h5 style="margin: 15px 0px">
          Total price of {{ getItemsQuantity(order) }} Items
        </h5>
        <p class="gray-bg flex-between">
          <span> Subtotal</span>
          <span class="primary">{{ order.subtotal }}</span>
        </p>
        <p class="gray-bg flex-between">
          <span> Percentage({{ order.supplier_percentage }}%)</span>
          <span class="primary">
            {{
              (
                parseFloat(order.subtotal) *
                (parseFloat(order.supplier_percentage) / 100)
              ).toFixed(2)
            }}</span
          >
        </p>

        <div class="gray-bg flex-between flex-start">
          <div>
            <span> Total</span><br />
            <span class="xs-text">
              ({{
                order.subtotal +
                " + " +
                (
                  parseFloat(order.subtotal) *
                  (parseFloat(order.supplier_percentage) / 100)
                ).toFixed(2) +
                ""
              }})
            </span>
          </div>
          <span class="primary"> {{ order.total }}</span>
        </div>

        <div class="gray-bg flex-between flex-start">
          <div>
            <span> Total in MMK </span><br />
            <span class="xs-text">
              ({{ order.total + " x " + order.money_price + "MMk" }})
            </span>
          </div>

          <span class="primary">
            {{ parseInt(order.total) * parseInt(order.money_price) }} MMK</span
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import couriors from "./couriors";
import kuaidi from "kuaidi";

export default {
  data() {
    return {
      order: {
        products: [],
      },
      loading_: null,
      images: [],
      index: null,
      openImage: false,
      gettingSale: true,
    };
  },
  created() {
    const test = kuaidi("YT8051031923607");
    console.log(" ~ file: Home.vue:302 ~ created ~ test:", test);
    const queries = new URLSearchParams(window.location.search);
    const sid = queries.get("sid");
    const oid = queries.get("oid");
    const date = queries.get("date");
    this.getOrder(sid, oid, date);
  },
  components: {},
  methods: {
    loaded(e) {
      e.path[0].style.height = e.path[0].clientWidth + "px";
    },
    viewImage(img) {
      this.images = [img];
      this.index = 0;
    },
    getItemsQuantity(sale) {
      return sale.products.reduce((total, item) => {
        return total + parseFloat(item.quantity);
      }, 0);
    },
    getLink(code) {
      var link = "";
      couriors.forEach((cr) => {
        cr.prefix.forEach((prefix) => {
          if (code.startsWith(prefix)) {
            link = cr.link + code;
          } else {
            link = "https://www.trackingmore.com/track/en/" + code;
          }
        });
      });
      return link;
    },
    getOrder(sid, oid, date) {
      this.gettingSale = true;
      var link = `https://osassist.pythonanywhere.com/api/sale-view/?sid=${sid}&oid=${oid}&date=${date}`;
      axios
        .get(link)
        .then((response) => {
          this.order = response.data;
          response.data.products.forEach((item) => {
            var img = new Image();
            img.src = item.image;
          });
          this.gettingSale = false;

          const loader = document.querySelector(".page-loader");
          loader.className += " hidden";
          setTimeout(() => {
            loader.className += " display_none";
          }, 1000);
        })
        .catch(() => {
          alert("Error!");
        });
    },
  },
};
</script>

    <style >
.loading-screen {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10000;
}
</style>